<template>
  <div class="loginDivClass">
    <el-card shadow="always" style="top:50px;margin: 0 auto;width: 29%;min-height: 650px;position: relative;">
      <div style="text-align:center;margin-top: 30px;">
        <img src="../../assets/huigong1.png" alt="logoImg" style="max-width: 90px;">
        <h3 style="font-weight: 400;margin-top: 20px;"></h3>
      </div>
      <div style="text-align: center;margin-left: 20px;margin-top: 50px;">
        <el-input placeholder="" v-model="inputUsername" clearable maxlength="10"
                  show-word-limit>
          <template slot="prepend"></template>
        </el-input>
        <el-input placeholder="" style="margin-top: 50px;" v-model="inputPassword" show-password maxlength="30"
                  show-word-limit>
          <template slot="prepend"><span style="padding-left: 7.2px;padding-right: 7.2px;"></span></template>
        </el-input>
      </div>
      <div style="text-align: center;margin-left: 10px;margin-top: 110px;">
        <el-button type="primary" style="min-width: 80%;" @click="login()"></el-button>
      </div>
      <el-row type="flex" class="row-bg" justify="center" style="top: 60px;">
        <el-col :span="24">
          <h6 style="text-align: center;color: #757575;"> </h6>
          <h6 style="text-align: center;color: #757575;">Copyright  2019</h6>
        </el-col>
      </el-row>
    </el-card>

    <!--      -->
    <el-dialog
      title=""
      :visible.sync="centerDialogVisible"
      width="30%"
      center>
      <div style="text-align: center">
        <h4>{{ dialogValue }}</h4>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="clickButton" style="margin-top: 30px;"> </el-button>
          </span>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  //state
  // import { mapMutations } from 'vuex'
  export default {
    name: "login",
    data() {
      return {
        "inputUsername": "",
        "inputPassword": "",
        "dialogValue": "",
        "centerDialogVisible": false,
        "loginButton": false
      }
    },
    methods: {
      // ...mapMutations(["isLogin","setUserId"]),
      login() {
        //
        this.centerDialogVisible = true;
        let jsonObj = {};
        //
        jsonObj.username = this.inputUsername;
        jsonObj.password = this.inputPassword;

        let jsonMsg = JSON.stringify(jsonObj);//
        console.log(jsonMsg);
        let self = this;
        //transaction
        //executeSqlSQL
        //transactionfunction
        // functionexecuteSql
        // function.
        this.$db.transaction(function (tx) {
          tx.executeSql('SELECT * FROM USER where account = "' + self.inputUsername + `"`, [], function (tx, results) {
            if (results.rows.length === 0) {
              alert("")
              return;
            }

            let pwd = results.rows.item(0).password;
            if (pwd !== self.inputPassword) {
              self.dialogValue = "";
            } else {
              self.dialogValue = "";
              console.log("")
              localStorage.setItem("userName", self.inputUsername);

            }

          }, function (e) {
            console.log(e);
          });
        });

      },
      clickButton() {
        if (this.dialogValue === "") {
          this.$router.push({path: "/", query: {userName: this.inputUsername}});
        } else {
          this.centerDialogVisible = false;
        }
      }
    }
  }
</script>

<style scoped>
  .loginDivClass {
    background: url("https://i.loli.net/2019/12/15/A2XS7tNrHCM9f4R.png");
    height: 792px;
  }
</style>



