<template>
  <div>
    <!--      -->
    <index-header></index-header>

    <!---->
    <div class="shopCarDiv">
      <h2><i class="el-icon-goods" style="color: rgb(92,182,255)"></i>&nbsp;</h2>
      <table style="display: block;padding: 10px 10px">
        <tr>
          <th></th>
          <th>Id</th>
          <th></th>
          <th></th>
        </tr>
        <tr v-for="(item,index) in shopCarList">
          <td><img :src="item.shopCarImg" style="width: 40px"></td>
          <td>{{item.shopCarId}}</td>
          <td>{{item.shopCarName}}</td>
          <td>{{item.shopCarPrice}}</td>
        </tr>

      </table>
      <el-row :gutter="20" style="margin-top: 20px;">
        <el-col :span="18" :offset="1">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[5, 10, 20, 40]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="tableData.length">
          </el-pagination>
        </el-col>
        <el-col :span="5">
          <el-button type="primary" @click="buyAll"></el-button>
        </el-col>
      </el-row>
    </div>

    <!--      -->
    <el-dialog
      title=""
      :visible.sync="centerDialogVisible"
      width="30%"
      center>
      <div style="text-align: center">
        <h4>{{ dialogValue }}</h4>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="clickButton" style="margin-top: 30px;"> </el-button>
          </span>
      </div>
    </el-dialog>

    <!--      -->
    <el-dialog
      title=""
      :visible.sync="centerDialogVisible2"
      width="30%"
      center>
      <div style="text-align: center">
        <h4>{{ dialogValue2 }}</h4>
        <img src="https://i.loli.net/2019/12/17/3rPjiINl9uS5Jdz.jpg" alt="" width="240" height="240">
        <br/>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="centerDialogVisible2 = false" style="margin-top: 30px;"></el-button>
          </span>
      </div>
    </el-dialog>

    <!--      -->
    <index-footer></index-footer>
  </div>
</template>

<script>
  import IndexHeader from './index-header'
  import IndexFooter from './index-footer'

  export default {
    name: "shopCar",
    data() {
      return {
        currentPage: 1, //
        pageSize: 5, //
        tableData: [],
        search: '',
        dialogValue: '',
        centerDialogVisible: false,
        centerDialogVisible2: false,
        dialogValue2: '',
        shopCarList: [],
      }
    },
    components: {
      IndexHeader, IndexFooter
    },
    mounted() {
      this.showShopCar();

      //
      let jsonObj = {};
      jsonObj.userId = window.sessionStorage.getItem("userId");
      let jsonMsg = JSON.stringify(jsonObj);
      let self = this;
      //
      $.get("http://localhost:8083/shopCar/findAllByUid.do", jsonObj, function (data) {
        self.tableData = data;
        //
        $(self.tableData).each(function (index, element) {
          let jsonObj = {};
          jsonObj.goodsId = element.goodsId;
          $.get("http://localhost:8083/goods/getGoodsMainImg.do", jsonObj, function (data) {
            //9090imgUrl
            element.picture = "http://localhost:9090/" + data.imgUrl;
            // Vue  Vue 
            self.$set(self.tableData, index, element);
          }, "json");
          //
          $.get("http://localhost:8083/goods/getGoodsById.do", jsonObj, function (data) {
            element.name = data.name;
            element.price = data.price;
            // Vue 
            self.$set(self.tableData, index, element);
          }, "json");
        });
      }, "json");
    },
    methods: {
      showShopCar() {
        let self = this;
        this.$db.transaction(function (tx) {
          tx.executeSql('SELECT * FROM SHOPCAR', [], function (tx, results) {
            let len = results.rows.length;
            // console.log(len);
            // console.log(results.rows);
            for (let i = 0; i < len; i++) {// 
              let shopCarId = results.rows.item(i).shopCarId;
              let shopCarImg = results.rows.item(i).shopCarImg;
              let shopCarPrice = results.rows.item(i).shopCarPrice;
              let shopCarName = results.rows.item(i).shopCarName;
              let shopCarBook = {};//js
              shopCarBook.shopCarId = shopCarId;
              shopCarBook.shopCarImg = shopCarImg;
              shopCarBook.shopCarPrice = shopCarPrice;
              shopCarBook.shopCarName = shopCarName;
              self.shopCarList.push(shopCarBook)
            }
            console.log(self.shopCarList)

          });//vue

        })
      },
      // currentPagepageSizedata
      handleSizeChange: function (size) {
        this.pageSize = size;
        // console.log(this.pageSize)  //
      },
      handleCurrentChange: function (currentPage) {
        this.currentPage = currentPage;
        // console.log(this.currentPage)  //
      },
      handleEdit(index, row) {
        //
        // console.log(index, row);
        let jsonObj = {};
        jsonObj.goodsId = row.goodsId;
        jsonObj.userId = window.sessionStorage.getItem("userId");
        let jsonMsg = JSON.stringify(jsonObj);
        let self = this;
        $.post("http://localhost:8083/shopCar/deleteOneByUidAndGid.do", jsonMsg, function (data) {
          if (data.code === 1) {
            self.dialogValue = "";
            self.initShopCar();
          } else {
            self.dialogValue = "";
          }
          self.centerDialogVisible = true;
        }, "json");
      },
      handleEdit2(index, row) {
        //
        // console.log(index, row);
        this.dialogValue2 = "" + row.price + "\r\n";
        this.centerDialogVisible2 = true;

        let self = this;

        let jsonObj = {};
        jsonObj.goodsId = row.goodsId;
        jsonObj.userId = window.sessionStorage.getItem("userId");
        let jsonMsg = JSON.stringify(jsonObj);

        $.post("http://localhost:8083/shopCar/deleteOneByUidAndGid.do", jsonMsg, function (data) {
          //
          if (data.code === 1) {
            //
            $.post("http://localhost:8083/orders/createOrder.do", jsonMsg, function (data) {
              if (data.code === 1) {
                //
                //15
                setTimeout(function () {
                  self.centerDialogVisible2 = false;
                  self.dialogValue = "";
                  self.centerDialogVisible = true;
                  self.initShopCar();
                }, 10000);
              } else {
                //
                //15
                setTimeout(function () {
                  self.centerDialogVisible2 = false;
                  self.dialogValue = "";
                  self.centerDialogVisible = true;
                  self.initShopCar();
                }, 10000);
              }
            }, "json");
          }
        }, "json");
      },
      clickButton() {
        this.centerDialogVisible = false;
      },
      buyAll() {
        let price = 0;
        for (let i = 0; i < this.tableData.length; i++) {
          price += this.tableData[i].price;
        }
        this.dialogValue2 = "" + price + "\r\n";
        this.centerDialogVisible2 = true;

        let self = this;
        let jsonObj = {};
        jsonObj.userId = window.sessionStorage.getItem("userId");
        let jsonMsg = JSON.stringify(jsonObj);
        //
      },
      initShopCar() {
        //
        let jsonObj = {};
        jsonObj.userId = window.sessionStorage.getItem("userId");
        let jsonMsg = JSON.stringify(jsonObj);
        let self = this;
        //
      },
    }
  }
</script>

<style scoped>
  .shopCarDiv {
    width: 80%;
    margin: 0 auto;
    padding-top: 80px;
  }

  tr {
    width: 100%;
  }

  th, td {
    text-align: center;
    width: 25%;
    padding: 10px 20px;
  }
</style>



