<template>
  <div class="registerDivClass">
    <el-card shadow="always" style="top:50px;margin: 0 auto;width: 29%;min-height: 650px;position: relative;">
      <div style="text-align:center;margin-top: 30px;">
        <img src="../../assets/huigong1.png" alt="logoImg" style="max-width: 90px;">
        <h3 style="font-weight: 400;margin-top: 20px;"></h3>
      </div>
      <div style="text-align: center;margin-left: 20px;margin-top: 50px;">
        <el-input placeholder=" 4-10 " v-model="inputUsername" clearable maxlength="10"
                  show-word-limit>
          <template slot="prepend"></template>
        </el-input>
        <el-input placeholder="1-8 " v-model="inputName" clearable maxlength="8"
                  show-word-limit style="margin-top: 30px;">
          <template slot="prepend"><span style="padding-left: 7.2px;padding-right: 7.2px;"></span></template>
        </el-input>
        <el-input placeholder=" +  +  6 " style="margin-top: 30px;" v-model="inputPassword" show-password
                  maxlength="30"
                  show-word-limit>
          <template slot="prepend"><span style="padding-left: 7.2px;padding-right: 7.2px;"></span></template>
        </el-input>
        <el-input placeholder="" v-model="inputEmail" clearable maxlength="30"
                  show-word-limit style="margin-top: 30px;">
          <template slot="prepend"><span style="padding-left: 7.2px;padding-right: 7.2px;"></span></template>
        </el-input>
      </div>
      <div style="text-align: center;margin-left: 10px;margin-top: 50px;">
        <el-button type="primary" style="min-width: 80%;" @click="login()"></el-button>
      </div>
      <el-row type="flex" class="row-bg" justify="center" style="top: 10px;">
        <el-col :span="24">
          <h6 style="text-align: center;color: #757575;"> </h6>
          <h6 style="text-align: center;color: #757575;">Copyright  2020</h6>
        </el-col>
      </el-row>
    </el-card>

    <!--      -->
    <el-dialog
      title=""
      :visible.sync="centerDialogVisible"
      width="30%"
      center>
      <div style="text-align: center">
        <h4>{{ dialogValue }}</h4>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="clickButton" style="margin-top: 30px;"> </el-button>
        </span>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "register",
    data() {
      return {
        "inputUsername": "",
        "inputPassword": "",
        "inputEmail": "",
        "inputName": "",
        "dialogValue": "",
        "centerDialogVisible": false,
        "registerButton": true,
        userName: ""
      }
    },
    //
    updated() {
      //
      if (this.check()) {
        this.registerButton = false;
      } else {
        this.registerButton = true;
      }
    },
    methods: {
      login() {

        let jsonObj = {};
        jsonObj.username = this.inputUsername;
        jsonObj.email = this.inputEmail;
        jsonObj.name = this.inputName;
        jsonObj.password = this.inputPassword;
        let jsonMsg = JSON.stringify(jsonObj);
        let self = this;
        //transaction
        //executeSqlSQL
        //transactionfunction
        // functionexecuteSql
        // function.
        this.$db.transaction(function (tx) {
          tx.executeSql('SELECT * FROM USER where account = "' + self.inputUsername + `"`, [], function (tx, results) {
            if (results.rows.length === 0) {
              tx.executeSql('INSERT INTO USER (account, password,nickName,mail) VALUES (?, ?, ?, ?)', [self.inputUsername, self.inputPassword, self.inputName, self.inputEmail], function () {
                //0 
                self.centerDialogVisible = true;
                self.dialogValue = "";
              }, function () {

              });
            } else {
              self.dialogValue = "";
              console.log("")
            }
          }, function (e) {
            console.log(e);
          });
        })
      },
      check() {
        //4-10
        let usernameRex = /^[a-zA-Z]{4,10}$/;
        //630
        let passwordRex = /^(?=.*[a-zA-Z])(?=.*\d)(?=.*[~!@#$%^&*()_+`\-={}:";'<>?,.\/]).{6,30}$/;
        //
        let emailRex = /^[A-Za-z0-9]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
        //1-8
        let nameRex = /^[a-zA-Z\u4e00-\u9fa5]{1,8}$/;
        if (usernameRex.test(this.inputUsername) && passwordRex.test(this.inputPassword)
          && emailRex.test(this.inputEmail) && nameRex.test(this.inputName))
          return true;
        else
          return false;
      },
      clickButton() {
        if (this.dialogValue === "") {
          // localStorage.setItem("userName",this.inputName);
          this.setItem("userName", this.inputName);
          this.$router.push("/")
          // this.$router.push({path: "/", query: {userName: this.inputUsername}});
        } else {
          this.centerDialogVisible = false;
        }
      },
      setItem(key, value) {
        localStorage.setItem(key, value);
        return true;
      }
    }
  }
</script>

<style scoped>
  .registerDivClass {
    background: url("https://i.loli.net/2019/12/15/A2XS7tNrHCM9f4R.png");
    height: 792px;
  }
</style>



